export class Link {

  name: string;
  url: string;
  qrea: boolean = false;
  id: string;
  active: boolean = false;
  
  public get selector(): string{
      return '#' + this.id;
  }

  constructor(name: string, url: string, id: string, qrea: boolean = false){
    this.name = name;
    this.url = url;
    this.qrea = qrea;
    this.id = id
  }

}


