import {Component, Inject} from '@angular/core';
import {DOCUMENT} from '@angular/platform-browser';
import {Router, NavigationEnd} from '@angular/router';
import {PageScrollService, PageScrollInstance, PageScrollConfig} from 'ng2-page-scroll';

@Component({selector: 'app-root', templateUrl: './app.component.html', styleUrls: ['./app.component.css']})
export class AppComponent {

  constructor(private router : Router, private pageScrollService : PageScrollService, @Inject(DOCUMENT)private document : Document) {

    PageScrollConfig.defaultDuration = 0;

    // on s'inscrit à l'évènement fin de navigation
    this
      .router
      .events
      .subscribe(val => {
        if (val instanceof NavigationEnd) {
          this.goToTop();
        }
      });

  }

  private goToTop() : void {
    let pageScrollInstance: PageScrollInstance = PageScrollInstance.simpleInstance(this.document, '#top');
    this
      .pageScrollService
      .start(pageScrollInstance);
  };

}



