import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { AngularFireModule } from 'angularfire2';
import {Ng2PageScrollModule} from 'ng2-page-scroll';

// import { NgSemanticModule } from "ng-semantic";
import { LandingComponent } from './landing/landing.component';
import { FooterComponent } from './footer/footer.component';
import { MentionsLegalesComponent } from './mentions-legales/mentions-legales.component';
import { CguComponent } from './cgu/cgu.component';
import { PresseComponent } from './presse/presse.component';
import { PageHeaderComponent } from './page-header/page-header.component';
import { MenubarTopFixedComponent } from './landing/menubar-top-fixed/menubar-top-fixed.component';
import { FactureComponent } from './facture/facture.component';
import { FactureHeaderComponent } from './facture/facture-header/facture-header.component';
import { FactureDetailComponent } from './facture/facture-detail/facture-detail.component';

// Must export the config
export const firebaseConfig = {
  apiKey: "AIzaSyBAeUUeF8qcmPE-gizdJd_xBZRs4Nkh9qo",
  authDomain: "qrea2-landing.firebaseapp.com",
  databaseURL: "https://qrea2-landing.firebaseio.com",
  storageBucket: "qrea2-landing.appspot.com",
  messagingSenderId: "192099670683"
};

const appRoutes: Routes = [
  { path: 'mentions-legales', component: MentionsLegalesComponent },
  { path: 'presse', component: PresseComponent },
  { path: 'cgu', component: CguComponent },
  { path: 'facture', component: FactureComponent },
  { path: '**', component: LandingComponent }
  //{ path: '',   redirectTo: '/landing', pathMatch: 'full' }
  // { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    LandingComponent,
    FooterComponent,
    MentionsLegalesComponent,
    CguComponent,
    PresseComponent,
    PageHeaderComponent,
    MenubarTopFixedComponent,
    FactureComponent,
    FactureHeaderComponent,
    FactureDetailComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    AngularFireModule.initializeApp(firebaseConfig),
    Ng2PageScrollModule.forRoot()
    // NgSemanticModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



