import {Component, OnInit, AfterViewInit} from '@angular/core';
import {Link} from './Link';

declare var jQuery;

@Component({selector: 'app-menubar-top-fixed', templateUrl: './menubar-top-fixed.component.html', styleUrls: ['./menubar-top-fixed.component.css']})

export class MenubarTopFixedComponent implements OnInit,
AfterViewInit {

  constructor() {}

  links : Array < Link > = [
    new Link('Qrea', '', 'qrea-top', true),
    new Link('Fonctionnalités', '', 'qrea-fonctionnalites'),
    new Link('Experts-Comptables', '', 'qrea-expert-comptable'),
    new Link('Equipe', '', 'qrea-equipe')
  ]

  ngOnInit() {}

  ngAfterViewInit() {

    // fix menu when passed
    jQuery('.masthead').visibility({
      once: false,
      onBottomPassed: function () {
        // jQuerylog.info("on a passé le .masthead");
        jQuery('#menuPublic').transition('fade in');
      },
      onBottomPassedReverse: function () {
        // jQuerylog.info("on revient sur le .masthead");
        jQuery('#menuPublic').transition('fade out');
      }
    });

    // initialisation des liens
    this
      .links
      .forEach(link => {
        this.initLink(link);
      });

  }

  private initLink(link : Link) {

    var self = this;

    var id = "#" + link.id;

    jQuery(id).visibility({
      once: false,
      onBottomPassed: function () {
        self.deselectLinkOnePage(link);
      },
      onBottomPassedReverse: function () {
        self.selectActiveLinkOnePage(link);
      },
      onTopPassed: function () {
        self.selectActiveLinkOnePage(link);
      },
      onTopPassedRevers: function () {
        self.deselectLinkOnePage(link);
      }
    });

  }

  private selectActiveLinkOnePage(link : Link) {

    this
      .links
      .forEach(l => {
        if (l === link) {
          l.active = true;
        } else {
          l.active = false;
        }
      });

  }

  private deselectLinkOnePage(link) {

    link.active = false;

  }

}



